body {
  margin: 0;
  padding: 0;
  height: 100vh;
  color: black;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /*background-image: linear-gradient(120deg, #84fab0 0%, #8fd3f4 100%);*/
  background-image: linear-gradient(120deg, #626b6b, #566061, #4b5457, #41494e, #383e44, #32353b, #2b2d33, #25252a, #1f1d21, #181618, #0f0d0e, #000000);
  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}




.productForm {

  background-color:  #ffffff;
  width : 60%;
  border-radius: 5px;
  
}


.inputSet {


  margin-top: 20px;

  
}


.bottom-save {


  height: 50px;
  line-height: 50px;
  background-color: #2358fb;
  color : #ffffff;
  text-align: center;
  cursor : pointer
  

}


.bottom-cancel {

  height: 50px;
  line-height: 50px;
  background-color: #333333;
  color : #ffffff;
  cursor : pointer


}


.main-image {


  opacity: 1;
  cursor : pointer


}


.secondary-image {


opacity: 0.5;
border-color : red;
cursor : pointer

}



.secondary-image:hover {


  opacity: 1;
  border-color: green;
  transition : opacity 1000ms ease-out;
  transition : opacity 1000ms ease-out;
  cursor : pointer
  
  }



  .main-image {

    opacity: 1;
    border-color: green;
    transition : opacity 1000ms ease-out;
    transition : opacity 1000ms ease-out;
    cursor : pointer
    


  }



  .bill-image{

    opacity: 1;
    border-color: green;



  }




